<template>
    <div class="full-screen" v-if="content.show" v-on:click="hideFullImg()" v-bind:style="setTop">
        <img v-bind:src="fullImg()" >
    </div>
</template>

<script>
export default {
    data () {
        return {
            
        }
    },
    props:['content'],
    methods: {
        // 引藏滿版圖片
        hideFullImg(){
            this.content.show = false;
            var mo=function(e){e.preventDefault();};//這是啥??
            document.body.style.overflow='';//出現滾動條
            document.removeEventListener("touchmove",mo,false);//禁止頁面滑動
        },
        // 用來更換滿版圖片
        fullImg(){
            return this.content.clickImg;
        }
    },
    computed: {
        // 滿版圖片要出現在當下使用者的觀看位置
        setTop(){
            if(this.content.show == true){return{top: window.pageYOffset + 'px'}}
            else{return{top: 0}}
        },
    },
}
</script>

<style scoped>
/* 滿版圖片區 垂直和水平接置中 設置z-index才可以在螢幕最前 */
.full-screen{
    position: absolute;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* 滿版圖片大小 */
.full-screen > img{
    height: 40vw;
    width: 25vw;
}
@media all and (max-width: 1280px){
    .full-screen > img{
        height: 73vw;
        width: 45vw;
    }
}
</style>